#renovation_text {
    opacity: 0;
}
#websites {
    opacity: 0;
    width: 100%;
    display: grid;
    grid-auto-flow: dense;
    grid-template:  "1fr 1fr 1fr 1fr 1fr" 33.33vh;
    grid-gap: 2px;
	-webkit-transition: .3s ease-in-out;
	transition: .3s ease-in-out;
}

#websites .website_box {
    display: inline-block;
    position: relative;
    overflow: hidden;
    color: white;
    background: #ccc;
    background-color: white;
}

#websites .website_box img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    transform: scale(1.0);
    transition: transform 0.4s ease;
}

#styleA,
#styleB {
    grid-column: span 2;
}

@media (max-width: 991px) {
    #websites {
        grid-template:  "1fr 1fr 1fr 1fr" 250px;
    }
}

@media (max-width: 768px) {
    #websites {
        grid-template:  "1fr" 200px
                        "1fr" 200px;
    }
}

.caption {
    position: absolute;
    bottom: 25px;
    left: 25px;
    opacity: 0;
    transition: transform 0.3s ease, opacity 0.3s ease;
    text-align: left;
    color: white;
}
  
.transparent_box {
    height: 100%;
    width: 100%;
    background-color:rgba(0, 0, 0, 0);
    position: absolute;
    top: 0;
    left: 0;
    transition: background-color 0.3s ease;
}
  
#websites .website_box:hover img { 
    transform: scale(1.1);
}
  
#websites .website_box:hover .transparent_box {
    background-color:rgba(0, 0, 0, 0.5);
}
  
#websites .website_box:hover .caption {
    transform: translateY(-20px);
    opacity: 1.0;
}
  
#websites .website_box:hover {
    cursor: pointer;
}
  
.caption > p:nth-child(2) {
    font-size: 0.8em;
}
  
.opacity_low {
    opacity: 0.5;
}
